


























import { Component, Vue } from 'vue-property-decorator';
import HelloWorld from './components/HelloWorld.vue';

@Component
export default class App extends Vue {
  public seconds = '0';
  get time(): string {
    let strsec = this.seconds;
    strsec = strsec.replace(',', '.');
    strsec = strsec.replace(/[^e\.\d]+/g, '');
    let seconds = parseFloat(strsec) || 0;
    this.seconds = seconds.toString();
    let minutes = 0;
    let hours = 0;
    let days = 0;
    let weeks = 0;
    let months = 0;
    let years = 0;
    if (!seconds) return '0s';
    let time = `${seconds}s`;
    if (seconds > 59) {
      minutes = Math.floor(seconds / 60);
      seconds -= minutes * 60;
      time = `${minutes}m${seconds}s`;
    }
    if (minutes && minutes > 59) {
      hours = Math.floor(minutes / 60);
      minutes -= hours * 60;
      time = `${hours}h${minutes}m${seconds}s`;
    }
    if (hours && hours > 23) {
      days = Math.floor(hours / 24);
      hours -= days * 24;
      time = `${days}d${hours}h${minutes}m${seconds}s`;
    }
    if (days && days > 6) {
      weeks = Math.floor(days / 7);
      days -= weeks * 7;
      time = `${weeks}w${days}d${hours}h${minutes}m${seconds}s`;
    }
    if (weeks && weeks > 3) {
      months = Math.floor(weeks / 4);
      weeks -= months * 4;
      time = `${months}M${weeks}w${days}d${hours}h${minutes}m${seconds}s`;
    }
    if (months && months > 11) {
      years = Math.floor(months / 12);
      months -= years * 12;
      time = `${years}y${months}M${weeks}w${days}d${hours}h${minutes}m${seconds}s`;
    }
    return time;
  }

  public tail(a: number) {
    let seconds = parseFloat(this.seconds);
    seconds *= 10;
    seconds += a;
    this.seconds = seconds.toString();
  }

  public clear() {
    this.seconds = '0';
  }
}
